.social{
    background: #f1f1f1;
    width: 32px;
    height: 32px;
    border-radius: 50%;
    text-align: center;
    display: inline-block;
}

.social-list{
    margin-left: 15px;
}

.profile-image {

  margin: 0 auto;
   float: none !important;
}

.sections-title{
    font-family: 'Montserrat', sans-serif;
     font-weight: 700;
     font-size: 24px;
}

.job-title-header{
    font-family: 'Montserrat', sans-serif;
    font-weight: 700;
    font-size: 16px;
}

.job-title-header .job-company-name{
    font-weight: normal;
    color: grey;
}

.job-title-header .job-company-name a{
    color: grey;
}

body{
    background-color: #d4e3e7;
    font-family: 'Lato', arial, sans-serif;
}

.project-img{
    width: 100%;
    height: auto;
}

.level-title {
    color: #000000;
    font-size: 16px;
